import { Component } from '@angular/core';
import { NavController, IonicPage } from 'ionic-angular';
import { Values } from '../../providers/service/values';
import { SearchService } from '../../providers/service/search-service';
//import {CartPage} from '../cart/cart';
//import { ProductPage } from '../product/product';
/*
  Generated class for the ProductPage page.

  See http://ionicframework.com/docs/v2/components/#navigation for more info on
  Ionic pages and navigation.
*/
@IonicPage()
@Component({
  templateUrl: 'search.html',
})
export class SearchPage {

  
  href:any;
  status:any;
  count: any;
  options:any;
  myInput: any;
  products: any;
  quantity:any;
  searchKey: any; 
  listview: boolean = false;
  shouldShowCancel: boolean = true;

 
  constructor(public nav: NavController, public service: SearchService, public values: Values) {
    this.options = [];
    this.quantity = "1";
    this.values.filter = {};
    this.myInput = "";
  }

  
  getCart(){
    this.nav.push('CartPage');
  }


  onInput($event){
    this.searchKey = $event.srcElement.value;
    this.values.filter = {};

    this.service.getSearch($event.srcElement.value, this.count, this.values.filter)
    .then((results) => this.products = results);
  }
  

  getProduct(href){
    this.nav.push('ProductPage', href);
  }

  
  encodeUrl(href) {
    return href.replace(/&amp;/g, '&');
  }

  
  doInfinite(href, infiniteScroll) {
    this.service.loadMore(href)
    .then((results) => this.handleMore(results, infiniteScroll));
  }

  
  handleMore(results, infiniteScroll){

    infiniteScroll.complete();
    if(results.products!=undefined){
      for (var i = 0; i < results.products.length; i++) {
        this.products.products.push(results.products[i]);
      };
    }

    this.products.current_page = results.current_page;
    this.products.total_page = results.total_page;
    this.products.next_page_url = results.next_page_url;
  }


  
  addToCart(id, href){

    if(this.values.cartItemCount[id] == undefined){this.values.cartItemCount[id] = 1} else{this.values.cartItemCount[id] += 1}; 

    var text = '{';
        var i;
        for (i = 0; i < this.options.length; i++) {
            var res = this.options[i].split(":");
            text += '"option['+res[0]+']":"'+res[1]+'",';
        }
      
    text += '"product_id":"' + id + '",';
    text += '"quantity":"' + this.quantity + '"}';

    var obj = JSON.parse(text);

    this.service.addToCart(obj)
    .then((results) => this.handleAddToCart(results, href));
  }

  
  handleAddToCart(results, href){
    if(results.error){
      this.nav.push('ProductPage', href);
    }
  }



  deleteFromCart(id){

    if(this.values.cartItemCount[id] != undefined && this.values.cartItemCount[id] == 0){this.values.cartItemCount[id] = 0} else{this.values.cartItemCount[id] -= 1};
    this.service.updateCart(id, this.values.cartItemCount[id])
    .then((results) => this.status = results);   
  }

  
  setListView(){
    this.values.listview = true;
  }

  setGridView(){
    this.values.listview = false;
  }
      
}



